@import "../../styles/utils/vars.scss";
@import "../../styles/utils/typography.scss";
@import "../../styles/utils/mixins.scss";

.HomePage {
  position: relative;
  display: flex;
  height: 1194px;

  &__slider {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__title {
    position: relative;
    width: calc(100% - #{2 * 32 + px});
    max-width: calc(#{$maxWidthContainer + px});
    margin-top: 270px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;

    @include tablet {
      margin-top: 200px;
    }

    @include iPad {
      margin-top: 214px;
    }

    @include mobile {
      width: calc(100% - #{2 * 16 + px});
      margin-top: 198px;
    }
  }

  &__h1 {
    @include h1;
    color: #fff;
  }

  &__subtitle {
    @include h4;
    color: #fff;
    padding-top: 16px;
  }

  &__bookNow-block {
    display: flex;
    height: 216px;
    margin-top: 170px;
    z-index: 5;
    margin-bottom: 270px;

    @include onDesktop {
      height: 176px;
    }

    @include tablet {
      margin-top: 140px;
      margin-bottom: 150px;
    }

    @include iPad {
      margin-top: 100px;
      margin-bottom: 75px;
    }

    @include mobile {
      flex-direction: column;
      height: max-content;
      width: calc(100% - #{2 * 16 + px});
      margin: 100px 16px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    box-sizing: border-box;
    height: 100%;
    width: 1303px;
    background-color: #fff;
    padding-left: 182px;
    gap: 40px;

    @include onDesktop {
      padding-left: 32px;
      width: 1035px;
    }

    @include tablet {
      padding-left: 32px;
      width: 886px;
    }

    @include iPad {
      padding-left: 32px;
      width: 443px;
    }

    @include mobile {
      flex-direction: column;
      border-radius: 16px;
      padding: 20px;
      width: 100%;
    }
  }

  &__bookNow-btn {
    display: flex;
    align-items: center;
    height: 100%;
    width: 170px;
    border-radius: 0 20px 20px 0;
    background-color: $orange;
    font-family: "Playfair Display", serif;
    font-size: 36px;
    line-height: 130%;
    color: #fff;
    padding: 0 20px;
    transition: all 0.4s ease;

    &:hover {
      color: $dark;
    }

    @include mobile {
      width: 100%;
      padding: 40px 20px;
    }
  }

  &__label {
    display: flex;
    flex-direction: column;
    width: 225px;
    color: $grey;
    font-size: 16px;
    text-transform: uppercase;

    @include onDesktop {
      width: 195px;
    }

    @include tablet {
      width: 158px;
    }

    @include iPad {
      width: 158px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__input {
    border: none;
    border-bottom: 1px solid #000;
    font-family: 'Playfair Display', serif;
    color: $dark;
    padding-top: 12px;
  }

  &__labelQuantity {
    cursor: pointer;

    // &--active {
    //   border-color: $orange;
    // }
  }

  &__chooseQuantity {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__inputQuantity {
    display: none;
  }

  &__decorQuantity {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    width: 32px;

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      height: 32px;
      width: 32px;
      border: 2px solid white;
      border-radius: 50%;
      background-color: transparent;
    }
  }
