.Swiper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 2;
  }

  &__slide {
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
  }

  &__img {
    display: block;
    width: 100%;
    height: 1194px;
  }
}
