@import "../../styles/utils/vars.scss";
@import "../../styles/utils/typography.scss";
@import "../../styles/utils/mixins.scss";

.Header {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  box-sizing: content-box;
  height: 128px;
  padding: 0 32px;
  width: calc(100% - #{2 * 32 + px});
  max-width: calc(#{$maxWidthContainer + px});
  justify-content: space-between;
  align-items: center;
  z-index: 5;

  @include iPad {
    height: 114px;
    padding: 0 20px;
  }

  @include mobile {
    height: 98px;
    padding: 0 16px;
  }

  &__logo {
    @include logo;
    z-index: 10;
  }

  &__nav {
    display: flex;
    gap: 32px;
    flex-wrap: wrap;
    row-gap: 10px;
    justify-content: center;
    padding: 0 15px;

    @media (max-width: 1023px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      position: fixed;
      left: -16px;
      right: -16px;
      top: 0;
      height: 100vh;
      background-color: $dark;
      padding: 0;
      overflow: hidden;
      gap: 36px;
      padding-top: 140px;
      transform: translateX(-200%);
      transition: all 0.4s ease;

      &--open {
        transform: translateX(0);
      }
    }
  }

  &__link {
    @include linkText;

    &--sign-up {
      &:hover {
        color: $dark;
      }
    }

    @media (max-width: 1023px) {
      font-size: 24px;
    }
  }

  &__hamburger {
    display: none;

    @media (max-width: 1023px) {
      display: block;
      background: url('../../images/icons/hamburger.svg') no-repeat center;
      background-size: contain;
      width: 37px;
      z-index: 10;

      &:hover {
        background: url('../../images/icons/menu_hover.svg') no-repeat center;
      }
    }

    &--open {
      background: url('../../images/icons/cross.svg') no-repeat center;

      &:hover {
        background: url('../../images/icons/cross.svg') no-repeat center;
      }
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    gap: 36px;
  }

  &__signUp {
    @include button;
  }
}
